import React,{useState, useEffect} from 'react';
// import {Link} from 'react-router-dom';
import {toast} from 'react-toastify';
import List from './shopping/List';
import ListInput from './shopping/Input';


const Dashboard = ({setAuth}) => {

  const [name, setName] = useState("");
  const [list, setList] = useState([]);
  const [isChanged, setIsChanged] = useState(false)
  const [sortChange, setSortChange] = useState(false)
  const [sortedList, setSortedList] = useState([]);

  const getData = async () => {
    try {
      console.log("another call")
      const res = await fetch('/dashboard', {
        headers: {"token": localStorage.token}
      })
      const data = await res.json();
      setName(data[0].user_name);
      setList(data.sort((a, b)=>(a.list_id > b.list_id ? 1 : -1)))
          
    } catch (error) {
      console.error(error.message)
    }
  }

  const handleSort = async (e, delta) => {
    const target = e.target.name
    if (delta === "up"){
      setSortedList(list.sort((a, b)=>(a[target] > b[target] ? -1 : 1)))
    } else if (delta === "down"){
      setSortedList(list.sort((a, b)=>(a[target] > b[target] ? 1 : -1)))
    }
    setSortChange(true)
  }

  const changeOrder = (cur_index, delta) => {
    let new_index = cur_index-delta;
    if (new_index === -1){
      new_index = list.length -1;
    }
    if (new_index === list.length) {
      new_index = 0
    }
    setSortedList(list.splice(new_index, 0, list.splice(cur_index, 1)[0]));
  };

  // const changeOrder = (arr, old_index, new_index) => {
  //   if (new_index >= arr.length) {
  //       var k = new_index - arr.length + 1;
  //       while (k--) {
  //           arr.push(undefined);
  //       }
  //   }
  //   arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
  //   return arr; // for testing
  // };


  useEffect(()=>{
    getData();
    setIsChanged(false)
  }, [isChanged])

  useEffect(()=>{
    setList(sortedList);
    setSortChange(false)
  }, [sortChange])

  const handleLogout = (e) => {
    e.preventDefault();
    localStorage.removeItem('token');
    setAuth(false)
    toast('You are now logged out')
  }

  const handleSave = async e => {
    e.preventDefault();
    try {
      await list.map((listItem, i)=>{
        console.log(i, listItem.item_id)
        const body = {i}
        fetch(`/dashboard/listsort/${listItem.item_id}`, {
        method: "PUT",
        headers: {"Content-Type": "application/json",
                  token: localStorage.token},
        body: JSON.stringify(body)
      });
      })
      
    } catch (error) {
      console.error(error.message)
    }
  }

  return (
    <div className="container p-3 my-5" style={{backgroundColor: "#f0f0f0", borderRadius: "10px"}} >
      <h3 className="text-center">{name}'s shopping list</h3>
      <div className="container">
        <ListInput {...{setIsChanged}}/>
        <List {...{list, setIsChanged, handleSort, changeOrder}} />
      </div>
      <span className="d-flex justify-content-between">
        <button className="btn btn-primary mt-5" onClick={(e)=>handleSave(e)}>Save order to database</button>
      <button className="btn btn-secondary mt-5" onClick={(e)=>handleLogout(e)}>Logout</button>
      </span>
      
    </div>
  )
}

export default Dashboard
