import React from 'react';
import {Link} from 'react-router-dom'

const Landing = () => {
  return (
    <div className="container d-flex justify-content-around my-5">
      <Link className="btn btn-primary my-5" style={{width: "25%", minWidth: "80px"}} to="/login" >Login</Link>
      <Link className="btn btn-primary my-5" style={{width: "25%", minWidth: "80px"}} to="/register" >Register</Link>
    </div>
  )
}

export default Landing
