import React, {Fragment, useState, useEffect} from 'react'
import categoryList from './categoryList'


const ListInput = ({setIsChanged}) => {

  const [values, setValues] = useState({
    item: "",
    category: ""
  })

  const {item, category} = values

  const handleChange = e => {
    setValues({
      ...values,
      [e.target.name]: e.target.value
    })
  };

  const handleSubmit = async e => {
    e.preventDefault();
    try {
      const body = {item, category}
      await fetch('/dashboard/list', {
        method: "POST",
        headers: {"Content-Type": "application/json",
                  token: localStorage.token},
        body:JSON.stringify(body)
      });
      setIsChanged(true)
      setValues({
        item: "",
        category: ""
      })
      document.querySelector("#item-input").focus();
    } catch (error) {
      console.error(error.message);
    }
  }

  const getFocus = () => {
    document.querySelector("#item-input").focus();
  }

  useEffect(()=>{
    getFocus()
  }, [])

  return (
    <Fragment>
      <form onSubmit={e=>handleSubmit(e)} className="my-5">
        <div className="form-row">
          <div className="col-6">
            <input className="form-control" id="item-input" placeholder="Item" type="text" name="item" value={item} onChange={e=>handleChange(e)} />
          </div>
          <div className="col-4">
          <select className="custom-select" name="category" value={category} required onChange={e=>handleChange(e)}>
            <option value="" disabled defaultValue hidden>Category</option>
            {categoryList.map((categoryOption, i)=>
              <option key={i} value={categoryOption} >{categoryOption}</option> )}
          </select>
            {/* <input className="form-control" placeholder="category" type="text" name="category" value={category} onChange={e=>handleChange(e)} /> */}
          </div>
          <div className="col-2">
            <button className="btn btn-primary btn-block" type="submit" onClick={e=>handleSubmit(e)} >Add to list</button>
          </div>
          
        </div>
        
      </form>
      
    </Fragment>
  )
}

export default ListInput
