import React, {useState, useEffect} from 'react'
import ListItem from './ListItem'

const List = ({list, setIsChanged, handleSort, changeOrder}) => {

  const [shoppingList, setShoppingList] = useState([])
  useEffect(()=>{
    setShoppingList(list)
  }, [list])

  return (
    <div className="container">
    <h1 className="text-center mt-5">Items:</h1>
      <div className='row mt-4' style={{lineHeight: "1.2rem"}} >
        <div className="col-6" style={{display: "flex"}} >
          <span style={{marginLeft: "1rem"}}>
            <button name="item"  style={{border:"none", display: "block"}} onClick={(e)=>handleSort(e, "up")} >&#9652;</button> 
            <button name="item" style={{border:"none", display: "block"}} onClick={(e)=>handleSort(e, "down")}>&#9662;</button>
          </span>
          <h3 style={{marginLeft: "3rem"}} >
            Item
          </h3>
          </div>
        <div className="col-4"  style={{display: "flex"}} >
          <span>
            <button name="category"  style={{border:"none", display: "block"}} onClick={(e)=>handleSort(e, "up")}  >&#9652;</button> 
            <button name="category" style={{border:"none", display: "block"}} onClick={(e)=>handleSort(e, "down")}>&#9662;</button>
          </span>
          <h3 style={{marginLeft: "1.5rem"}} >
            Category
          </h3>
        </div>
        <div className="col-2" style={{display: "flex", justifyContent: "space-evenly"}} >
          <h3>Action</h3>
        </div>
      </div>
      { shoppingList.length !== 0
      && shoppingList[0].item_id !== null
      && shoppingList.map((listItem, i)=>
        <ListItem 
          key={listItem.item_id}
          {...{i, listItem, setIsChanged, changeOrder}}/>)}
    </div>
  )
}

export default List
