import React, {useState, useEffect} from 'react'
import Edit from './Edit';

const ListItem = ({i, listItem, setIsChanged, changeOrder}) => {

  const [checked, setChecked] = useState(false)

  useEffect(()=>{
    setChecked(listItem.obtained)
  }, [])

  const handleObtained = async e =>{
    setChecked(e.target.checked)
    try {
      let obtained = await e.target.checked
      const body = {obtained}
      await fetch(`/dashboard/list/obtained/${listItem.item_id}`, {
        method: "PUT",
        headers: {"Content-Type": "application/json",
                  token: localStorage.token},
        body: JSON.stringify(body)
      });
      // setIsChanged(true);
      console.log("obtained: ", obtained)
    } catch (error) {
      console.error(error.message)
    }
  }

  return (
    <div className="row" style={{border: "1px solid grey", lineHeight: "1rem"}}>
      <div className="col-6" style={{display: "flex"}} >
        
        <input type="checkbox" checked={checked} onChange={e=>handleObtained(e)} />
        
        <span style={{marginLeft: ".5rem"}} >
          <button onClick={()=>changeOrder(i, +1)} style={{border:"none", display: "block"}} >&#9652;</button> 
          <button onClick={()=>changeOrder(i, -1)} style={{border:"none", display: "block"}} >&#9662;</button>
        </span>
        <span
          style={
            {lineHeight: "2rem",
            marginLeft: "3rem", 
            textAlign: "center", 
            textDecoration: checked
            ? "line-through"
            : "none",
            color: checked
            ? " #808b96 "
            : "inherit"}}>
          {listItem.item}
        </span>
        </div>
      <div 
        className="col-4" 
        style={
          {lineHeight: "2rem", 
          textAlign: "center",
          textDecoration: checked
            ? "line-through"
            : "none",
          color: checked
            ? " #808b96 "
            : "inherit"}} >
          {listItem.category}</div>
      <div className="col-2" ><Edit {...{listItem, setIsChanged}} /></div>
    </div>
        
    
    
  )
}

export default ListItem
