import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import {toast} from 'react-toastify';

const Register = ({setAuth}) => {

  const [values, setValues] = useState({
    name: "",
    email: "",
    password: ""
  })
  const {name, email, password} = values;

  const handleChange = e => {
    setValues({
      ...values,
      [e.target.name]: e.target.value
    })
  }

  const handleSubmit = async e => {
    e.preventDefault();
    try {
      const body = {name, email, password};
      const res = await fetch('/auth/register', {
        method: "POST",
        headers: {"Content-Type": "application/json"},
        body: JSON.stringify(body)
      })
      const parsedRes = await res.json();
      if (parsedRes.token){
        localStorage.setItem('token', parsedRes.token);
        setAuth(true);
        toast.success('registration successful!')
      } else {
        setAuth(false)
        toast.error(parsedRes)
      }
    } catch (error) {
      console.log(error.message)
    }
  }

  return (
    <div className="container p-3 my-5" style={{backgroundColor: "#f0f0f0", borderRadius: "10px"}}>
      <h2 className="text-center">Registration</h2>
      <p className="text-center">fill in your details below to begin...</p>
      <form onSubmit={e=>handleSubmit(e)}>
        <input className="form-control my-3" type="text" name="name" value={name} placeholder="name" onChange={e=>{handleChange(e)}}/>
        <input className="form-control my-3" type="email" name="email" value={email} placeholder="email" onChange={e=>{handleChange(e)}}/>
        <input className="form-control my-3" type="password" name="password" value={password} placeholder="password" onChange={e=>{handleChange(e)}}/>
        <button className="btn btn-primary btn-block">Register</button>
      </form>
      <Link to="/login">Login</Link>
    </div>
  )
}

export default Register
